nav {
  min-height: 10vh;
  padding: 20px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  button {
    background: transparent;
    border: none ;
    

    cursor: pointer;
    padding: 0.5rem;
    border: 2px solid rgb(65, 65, 65);
    transition: all 0.3s ease;
    &:hover {
      background: rgb(65, 65, 65);
      color: white;
    }
  }
  .library-active {
    background: rgb(65, 65, 65);
    color: white;

  }


}

a.social{
  margin: 0 1rem;
  transition: transform 250mc;
  display: inline-block;
}

a.social:hover{
  transform: translateY(-2px);
}

.social-container p {
  text-align: center;
}

a.youtube{
  color: #FF0000;
}

a.insta{
  color: black;
}


@media screen and (max-width: 768px) {
  nav {
    button {
      z-index: 10;
    }
  }
}
